@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

body,
p,
main,
section,
article,
h2,
div,
a,
header,
nav,
ul {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  font-family: "Roboto", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #060b35;
}
